"use client";

import { useProjects } from '@/context/ProjectContext';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { Button } from '@/components/ui/button';
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table';
import { Eye, Edit, Trash2 } from 'lucide-react';
import { useState } from 'react';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger } from '@/components/ui/dialog';
import { Project } from '@/types';

export default function ProjectSummary() {
  const { projects, deleteProject, getProjectSummaries } = useProjects();
  const [selectedProject, setSelectedProject] = useState<Project | null>(null);
  
  const summaries = getProjectSummaries();

  const formatCurrency = (amount: number) => {
    return new Intl.NumberFormat('es-PE', {
      style: 'currency',
      currency: 'PEN'
    }).format(amount);
  };

  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('es-PE');
  };

  const getExpenseDetails = (project: Project) => {
    const expenses = project.expenses;
    const totals = {
      movilidad: expenses.reduce((sum, e) => sum + (e.movilidad?.monto || 0), 0),
      viaticos: expenses.reduce((sum, e) => {
        let total = 0;
        if (e.viaticos?.hospedaje) total += e.viaticos.hospedaje.monto;
        if (e.viaticos?.alimento) {
          total += Object.values(e.viaticos.alimento).reduce((t, val) => t + (val || 0), 0);
        }
        return sum + total;
      }, 0),
      compras: expenses.reduce((sum, e) => sum + (e.compras?.monto || 0), 0)
    };

    return { expenses, totals };
  };

  return (
    <div className="space-y-6">
      {summaries.length === 0 ? (
        <Card>
          <CardContent className="flex items-center justify-center py-12">
            <div className="text-center">
              <h3 className="text-lg font-medium text-muted-foreground mb-2">
                No hay proyectos registrados
              </h3>
              <p className="text-sm text-muted-foreground">
                Crea tu primer proyecto en la pestaña de Registro
              </p>
            </div>
          </CardContent>
        </Card>
      ) : (
        <>
          <Card>
            <CardHeader>
              <CardTitle>Resumen General</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                <div className="text-center p-4 bg-muted rounded-lg">
                  <div className="text-2xl font-bold">{summaries.length}</div>
                  <div className="text-sm text-muted-foreground">Proyectos Totales</div>
                </div>
                <div className="text-center p-4 bg-muted rounded-lg">
                  <div className="text-2xl font-bold">
                    {formatCurrency(summaries.reduce((sum, s) => sum + s.montoTotal, 0))}
                  </div>
                  <div className="text-sm text-muted-foreground">Gasto Total</div>
                </div>
                <div className="text-center p-4 bg-muted rounded-lg">
                  <div className="text-2xl font-bold">
                    {formatCurrency(summaries.reduce((sum, s) => sum + s.montoTotal, 0) / summaries.length)}
                  </div>
                  <div className="text-sm text-muted-foreground">Promedio por Proyecto</div>
                </div>
              </div>
            </CardContent>
          </Card>

          <Card>
            <CardHeader>
              <CardTitle>Lista de Proyectos</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="overflow-x-auto">
                <Table>
                  <TableHeader>
                    <TableRow>
                      <TableHead>SOT</TableHead>
                      <TableHead>Cliente</TableHead>
                      <TableHead>Fecha</TableHead>
                      <TableHead>Lugar</TableHead>
                      <TableHead>Área</TableHead>
                      <TableHead>Sub Área</TableHead>
                      <TableHead className="text-right">Monto Total</TableHead>
                      <TableHead className="text-center">Acciones</TableHead>
                    </TableRow>
                  </TableHeader>
                  <TableBody>
                    {summaries.map((summary) => {
                      const project = projects.find(p => p.id === summary.id);
                      return (
                        <TableRow key={summary.id}>
                          <TableCell className="font-medium">{summary.sot}</TableCell>
                          <TableCell>{summary.cliente}</TableCell>
                          <TableCell>{formatDate(summary.fecha)}</TableCell>
                          <TableCell>{summary.lugar}</TableCell>
                          <TableCell>
                            <Badge variant="outline">{summary.area}</Badge>
                          </TableCell>
                          <TableCell>
                            <Badge variant="secondary">{summary.subArea}</Badge>
                          </TableCell>
                          <TableCell className="text-right font-medium">
                            {formatCurrency(summary.montoTotal)}
                          </TableCell>
                          <TableCell className="text-center">
                            <div className="flex justify-center gap-2">
                              <Dialog>
                                <DialogTrigger asChild>
                                  <Button
                                    size="sm"
                                    variant="outline"
                                    onClick={() => setSelectedProject(project || null)}
                                  >
                                    <Eye className="h-4 w-4" />
                                  </Button>
                                </DialogTrigger>
                                <DialogContent className="max-w-4xl max-h-[80vh] overflow-y-auto">
                                  <DialogHeader>
                                    <DialogTitle>
                                      Detalles del Proyecto: {project?.sot} - {project?.cliente}
                                    </DialogTitle>
                                  </DialogHeader>
                                  {project && (
                                    <div className="space-y-6">
                                      <div className="grid grid-cols-2 gap-4">
                                        <div>
                                          <h4 className="font-medium text-sm text-muted-foreground">Personal</h4>
                                          <p>{project.personal}</p>
                                        </div>
                                        <div>
                                          <h4 className="font-medium text-sm text-muted-foreground">Lugar</h4>
                                          <p>{project.lugar}</p>
                                        </div>
                                        <div>
                                          <h4 className="font-medium text-sm text-muted-foreground">Fecha</h4>
                                          <p>{formatDate(project.fecha)}</p>
                                        </div>
                                        <div>
                                          <h4 className="font-medium text-sm text-muted-foreground">Área</h4>
                                          <p>{project.area}</p>
                                        </div>
                                        <div>
                                          <h4 className="font-medium text-sm text-muted-foreground">Sub Área</h4>
                                          <p>{project.subArea}</p>
                                        </div>
                                        <div>
                                          <h4 className="font-medium text-sm text-muted-foreground">Total de Gastos</h4>
                                          <p className="font-bold">{formatCurrency(summary.montoTotal)}</p>
                                        </div>
                                      </div>

                                      {project.expenses.length > 0 && (
                                        <div>
                                          <h4 className="font-medium mb-4">Desglose de Gastos</h4>
                                          <div className="space-y-4">
                                            {project.expenses.map((expense, index) => (
                                              <div key={expense.id} className="border rounded-lg p-4">
                                                <div className="font-medium mb-2">{formatDate(expense.fecha)}</div>
                                                <div className="space-y-2">
                                                  {expense.movilidad && (
                                                    <div>
                                                      <Badge variant="outline" className="mb-1">Movilidad</Badge>
                                                      <div className="text-sm">
                                                        {expense.movilidad.inicio} → {expense.movilidad.destino}
                                                        <span className="ml-2 font-medium">{formatCurrency(expense.movilidad.monto)}</span>
                                                      </div>
                                                    </div>
                                                  )}
                                                  {expense.viaticos && (
                                                    <div>
                                                      <Badge variant="outline" className="mb-1">Viáticos</Badge>
                                                      <div className="text-sm space-y-1">
                                                        {expense.viaticos.hospedaje && (
                                                          <div>
                                                            Hospedaje: {expense.viaticos.hospedaje.lugar} - {formatCurrency(expense.viaticos.hospedaje.monto)}
                                                          </div>
                                                        )}
                                                        {expense.viaticos.alimento && (
                                                          <div>
                                                            Alimentos: 
                                                            {expense.viaticos.alimento.desayuno && ` Desayuno ${formatCurrency(expense.viaticos.alimento.desayuno)}`}
                                                            {expense.viaticos.alimento.almuerzo && ` Almuerzo ${formatCurrency(expense.viaticos.alimento.almuerzo)}`}
                                                            {expense.viaticos.alimento.cena && ` Cena ${formatCurrency(expense.viaticos.alimento.cena)}`}
                                                          </div>
                                                        )}
                                                      </div>
                                                    </div>
                                                  )}
                                                  {expense.compras && (
                                                    <div>
                                                      <Badge variant="outline" className="mb-1">Compras</Badge>
                                                      <div className="text-sm">
                                                        {expense.compras.descripcion}
                                                        <span className="ml-2 font-medium">{formatCurrency(expense.compras.monto)}</span>
                                                      </div>
                                                    </div>
                                                  )}
                                                </div>
                                              </div>
                                            ))}
                                          </div>
                                        </div>
                                      )}
                                    </div>
                                  )}
                                </DialogContent>
                              </Dialog>
                              <Button
                                size="sm"
                                variant="destructive"
                                onClick={() => {
                                  if (confirm('¿Estás seguro de que quieres eliminar este proyecto?')) {
                                    deleteProject(summary.id);
                                  }
                                }}
                              >
                                <Trash2 className="h-4 w-4" />
                              </Button>
                            </div>
                          </TableCell>
                        </TableRow>
                      );
                    })}
                  </TableBody>
                </Table>
              </div>
            </CardContent>
          </Card>
        </>
      )}
    </div>
  );
}
