"use client";

import { useProjects } from '@/context/ProjectContext';
import { useRouter } from 'next/navigation';
import { useEffect } from 'react';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import ProjectRegistration from '@/components/ProjectRegistration';
import ProjectSummary from '@/components/ProjectSummary';

export default function Home() {
  const { currentUser } = useProjects();
  const router = useRouter();

  useEffect(() => {
    if (!currentUser) {
      router.push('/auth');
    }
  }, [currentUser, router]);

  if (!currentUser) {
    return null;
  }

  return (
    <div className="container mx-auto p-6 max-w-6xl">
      <div className="mb-8">
        <div className="flex justify-between items-center">
          <div>
            <h1 className="text-3xl font-bold mb-2">Registro de Gastos</h1>
            <p className="text-muted-foreground">Sistema de gestión de gastos por proyecto</p>
          </div>
          <div className="text-right">
            <p className="text-sm text-muted-foreground">Bienvenido, {currentUser.name}</p>
            <Button 
              variant="outline" 
              size="sm"
              onClick={() => {
                const { logout } = useProjects();
                logout();
              }}
            >
              Cerrar Sesión
            </Button>
          </div>
        </div>
      </div>

      <Tabs defaultValue="registro" className="w-full">
        <TabsList className="grid w-full grid-cols-2 mb-6">
          <TabsTrigger value="registro" className="text-lg py-3">
            Registro
          </TabsTrigger>
          <TabsTrigger value="resumen" className="text-lg py-3">
            Resumen
          </TabsTrigger>
        </TabsList>

        <TabsContent value="registro">
          <Card>
            <CardHeader>
              <CardTitle>Registro de Proyectos y Gastos</CardTitle>
            </CardHeader>
            <CardContent>
              <ProjectRegistration />
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="resumen">
          <Card>
            <CardHeader>
              <CardTitle>Resumen de Proyectos</CardTitle>
            </CardHeader>
            <CardContent>
              <ProjectSummary />
            </CardContent>
          </Card>
        </TabsContent>
      </Tabs>
    </div>
  );
}
