"use client";

import { useState } from 'react';
import { useProjects } from '@/context/ProjectContext';
import { Project, Expense } from '@/types';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Textarea } from '@/components/ui/textarea';
import { Separator } from '@/components/ui/separator';
import { Badge } from '@/components/ui/badge';
import { Checkbox } from '@/components/ui/checkbox';
import { Trash2, Edit, Plus } from 'lucide-react';

export default function ProjectRegistration() {
  const { projects, addProject, updateProject, deleteProject, addExpense, updateExpense, deleteExpense } = useProjects();
  const [selectedProject, setSelectedProject] = useState<string>('');
  const [editingProject, setEditingProject] = useState<string | null>(null);
  const [editingExpense, setEditingExpense] = useState<string | null>(null);

  // Project form state
  const [projectForm, setProjectForm] = useState({
    sot: '',
    cliente: '',
    personal: '',
    lugar: '',
    fecha: '',
    area: '' as 'pint provincia' | 'mantenimiento provincia',
    subArea: '' as 'instalacion' | 'baja' | 'mantenimiento'
  });

  // Expense form state
  const [expenseForm, setExpenseForm] = useState({
    fecha: '',
    movilidad: {
      enabled: false,
      inicio: '',
      destino: '',
      monto: 0
    },
    viaticos: {
      enabled: false,
      hospedaje: {
        lugar: '',
        monto: 0
      },
      alimento: {
        desayuno: 0,
        almuerzo: 0,
        cena: 0
      }
    },
    compras: {
      enabled: false,
      descripcion: '',
      monto: 0
    }
  });

  const resetProjectForm = () => {
    setProjectForm({
      sot: '',
      cliente: '',
      personal: '',
      lugar: '',
      fecha: '',
      area: '' as any,
      subArea: '' as any
    });
    setEditingProject(null);
  };

  const resetExpenseForm = () => {
    setExpenseForm({
      fecha: '',
      movilidad: {
        enabled: false,
        inicio: '',
        destino: '',
        monto: 0
      },
      viaticos: {
        enabled: false,
        hospedaje: {
          lugar: '',
          monto: 0
        },
        alimento: {
          desayuno: 0,
          almuerzo: 0,
          cena: 0
        }
      },
      compras: {
        enabled: false,
        descripcion: '',
        monto: 0
      }
    });
    setEditingExpense(null);
  };

  const handleProjectSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (editingProject) {
      updateProject(editingProject, projectForm);
    } else {
      addProject(projectForm);
    }
    resetProjectForm();
  };

  const handleExpenseSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (!selectedProject) return;

    const expense: Omit<Expense, 'id'> = {
      projectId: selectedProject,
      fecha: expenseForm.fecha,
      ...(expenseForm.movilidad.enabled && {
        movilidad: {
          inicio: expenseForm.movilidad.inicio,
          destino: expenseForm.movilidad.destino,
          monto: expenseForm.movilidad.monto
        }
      }),
      ...(expenseForm.viaticos.enabled && {
        viaticos: {
          ...(expenseForm.viaticos.hospedaje.monto > 0 && {
            hospedaje: {
              lugar: expenseForm.viaticos.hospedaje.lugar,
              monto: expenseForm.viaticos.hospedaje.monto
            }
          }),
          ...(Object.values(expenseForm.viaticos.alimento).some(v => v > 0) && {
            alimento: {
              ...(expenseForm.viaticos.alimento.desayuno > 0 && { desayuno: expenseForm.viaticos.alimento.desayuno }),
              ...(expenseForm.viaticos.alimento.almuerzo > 0 && { almuerzo: expenseForm.viaticos.alimento.almuerzo }),
              ...(expenseForm.viaticos.alimento.cena > 0 && { cena: expenseForm.viaticos.alimento.cena })
            }
          })
        }
      }),
      ...(expenseForm.compras.enabled && {
        compras: {
          descripcion: expenseForm.compras.descripcion,
          monto: expenseForm.compras.monto
        }
      })
    };

    if (editingExpense) {
      updateExpense(selectedProject, editingExpense, expense);
    } else {
      addExpense(selectedProject, expense);
    }
    resetExpenseForm();
  };

  const handleEditProject = (project: Project) => {
    setProjectForm({
      sot: project.sot,
      cliente: project.cliente,
      personal: project.personal,
      lugar: project.lugar,
      fecha: project.fecha,
      area: project.area,
      subArea: project.subArea
    });
    setEditingProject(project.id);
  };

  const handleEditExpense = (expense: Expense) => {
    const resetForm = {
      fecha: expense.fecha,
      movilidad: {
        enabled: !!expense.movilidad,
        inicio: expense.movilidad?.inicio || '',
        destino: expense.movilidad?.destino || '',
        monto: expense.movilidad?.monto || 0
      },
      viaticos: {
        enabled: !!expense.viaticos,
        hospedaje: {
          lugar: expense.viaticos?.hospedaje?.lugar || '',
          monto: expense.viaticos?.hospedaje?.monto || 0
        },
        alimento: {
          desayuno: expense.viaticos?.alimento?.desayuno || 0,
          almuerzo: expense.viaticos?.alimento?.almuerzo || 0,
          cena: expense.viaticos?.alimento?.cena || 0
        }
      },
      compras: {
        enabled: !!expense.compras,
        descripcion: expense.compras?.descripcion || '',
        monto: expense.compras?.monto || 0
      }
    };

    setExpenseForm(resetForm);
    setEditingExpense(expense.id);
  };

  const selectedProjectData = projects.find(p => p.id === selectedProject);

  return (
    <div className="space-y-6">
      <Tabs defaultValue="projects" className="w-full">
        <TabsList className="grid w-full grid-cols-2">
          <TabsTrigger value="projects">Proyectos</TabsTrigger>
          <TabsTrigger value="expenses">Gastos</TabsTrigger>
        </TabsList>

        <TabsContent value="projects" className="space-y-4">
          <Card>
            <CardHeader>
              <CardTitle>{editingProject ? 'Editar Proyecto' : 'Nuevo Proyecto'}</CardTitle>
            </CardHeader>
            <CardContent>
              <form onSubmit={handleProjectSubmit} className="space-y-4">
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  <div>
                    <Label htmlFor="sot">SOT</Label>
                    <Input
                      id="sot"
                      value={projectForm.sot}
                      onChange={(e) => setProjectForm(prev => ({ ...prev, sot: e.target.value }))}
                      required
                    />
                  </div>
                  <div>
                    <Label htmlFor="cliente">Cliente</Label>
                    <Input
                      id="cliente"
                      value={projectForm.cliente}
                      onChange={(e) => setProjectForm(prev => ({ ...prev, cliente: e.target.value }))}
                      required
                    />
                  </div>
                  <div>
                    <Label htmlFor="personal">Personal</Label>
                    <Input
                      id="personal"
                      value={projectForm.personal}
                      onChange={(e) => setProjectForm(prev => ({ ...prev, personal: e.target.value }))}
                      required
                    />
                  </div>
                  <div>
                    <Label htmlFor="lugar">Lugar</Label>
                    <Input
                      id="lugar"
                      value={projectForm.lugar}
                      onChange={(e) => setProjectForm(prev => ({ ...prev, lugar: e.target.value }))}
                      required
                    />
                  </div>
                  <div>
                    <Label htmlFor="fecha">Fecha</Label>
                    <Input
                      id="fecha"
                      type="date"
                      value={projectForm.fecha}
                      onChange={(e) => setProjectForm(prev => ({ ...prev, fecha: e.target.value }))}
                      required
                    />
                  </div>
                  <div>
                    <Label htmlFor="area">Área</Label>
                    <Select
                      value={projectForm.area}
                      onValueChange={(value: 'pint provincia' | 'mantenimiento provincia') => 
                        setProjectForm(prev => ({ ...prev, area: value }))
                      }
                    >
                      <SelectTrigger>
                        <SelectValue placeholder="Selecciona el área" />
                      </SelectTrigger>
                      <SelectContent>
                        <SelectItem value="pint provincia">Pint Provincia</SelectItem>
                        <SelectItem value="mantenimiento provincia">Mantenimiento Provincia</SelectItem>
                      </SelectContent>
                    </Select>
                  </div>
                  <div>
                    <Label htmlFor="subArea">Sub Área</Label>
                    <Select
                      value={projectForm.subArea}
                      onValueChange={(value: 'instalacion' | 'baja' | 'mantenimiento') => 
                        setProjectForm(prev => ({ ...prev, subArea: value }))
                      }
                    >
                      <SelectTrigger>
                        <SelectValue placeholder="Selecciona la sub área" />
                      </SelectTrigger>
                      <SelectContent>
                        <SelectItem value="instalacion">Instalación</SelectItem>
                        <SelectItem value="baja">Baja</SelectItem>
                        <SelectItem value="mantenimiento">Mantenimiento</SelectItem>
                      </SelectContent>
                    </Select>
                  </div>
                </div>
                <div className="flex gap-2">
                  <Button type="submit">
                    {editingProject ? 'Actualizar' : 'Crear'} Proyecto
                  </Button>
                  {editingProject && (
                    <Button type="button" variant="outline" onClick={resetProjectForm}>
                      Cancelar
                    </Button>
                  )}
                </div>
              </form>
            </CardContent>
          </Card>

          <Card>
            <CardHeader>
              <CardTitle>Proyectos Existentes</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="space-y-2">
                {projects.map((project) => (
                  <div key={project.id} className="flex items-center justify-between p-3 border rounded-lg">
                    <div>
                      <div className="font-medium">{project.sot} - {project.cliente}</div>
                      <div className="text-sm text-muted-foreground">
                        {project.area} - {project.subArea} | {project.lugar} | {project.fecha} | {project.personal}
                      </div>
                    </div>
                    <div className="flex gap-2">
                      <Button
                        size="sm"
                        variant="outline"
                        onClick={() => handleEditProject(project)}
                      >
                        <Edit className="h-4 w-4" />
                      </Button>
                      <Button
                        size="sm"
                        variant="destructive"
                        onClick={() => deleteProject(project.id)}
                      >
                        <Trash2 className="h-4 w-4" />
                      </Button>
                    </div>
                  </div>
                ))}
              </div>
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="expenses" className="space-y-4">
          <Card>
            <CardHeader>
              <CardTitle>Seleccionar Proyecto</CardTitle>
            </CardHeader>
            <CardContent>
              <Select value={selectedProject} onValueChange={setSelectedProject}>
                <SelectTrigger>
                  <SelectValue placeholder="Selecciona un proyecto" />
                </SelectTrigger>
                <SelectContent>
                  {projects.map((project) => (
                    <SelectItem key={project.id} value={project.id}>
                      {project.sot} - {project.cliente} ({project.area} - {project.subArea})
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </CardContent>
          </Card>

          {selectedProject && (
            <>
              <Card>
                <CardHeader>
                  <CardTitle>{editingExpense ? 'Editar Gasto' : 'Nuevo Gasto'}</CardTitle>
                </CardHeader>
                <CardContent>
                  <form onSubmit={handleExpenseSubmit} className="space-y-4">
                    <div>
                      <Label htmlFor="fecha">Fecha del Gasto</Label>
                      <Input
                        id="fecha"
                        type="date"
                        value={expenseForm.fecha}
                        onChange={(e) => setExpenseForm(prev => ({ ...prev, fecha: e.target.value }))}
                        required
                      />
                    </div>

                    <div className="space-y-4">
                      <div className="border rounded-lg p-4">
                        <div className="flex items-center space-x-2 mb-4">
                          <Checkbox
                            id="movilidad"
                            checked={expenseForm.movilidad.enabled}
                            onCheckedChange={(checked) => 
                              setExpenseForm(prev => ({ 
                                ...prev, 
                                movilidad: { ...prev.movilidad, enabled: checked as boolean } 
                              }))
                            }
                          />
                          <Label htmlFor="movilidad" className="font-medium">Movilidad</Label>
                        </div>
                        {expenseForm.movilidad.enabled && (
                          <div className="grid grid-cols-1 md:grid-cols-3 gap-4 ml-6">
                            <div>
                              <Label htmlFor="movilidad-inicio">Inicio</Label>
                              <Input
                                id="movilidad-inicio"
                                value={expenseForm.movilidad.inicio}
                                onChange={(e) => setExpenseForm(prev => ({ 
                                  ...prev, 
                                  movilidad: { ...prev.movilidad, inicio: e.target.value } 
                                }))}
                              />
                            </div>
                            <div>
                              <Label htmlFor="movilidad-destino">Destino</Label>
                              <Input
                                id="movilidad-destino"
                                value={expenseForm.movilidad.destino}
                                onChange={(e) => setExpenseForm(prev => ({ 
                                  ...prev, 
                                  movilidad: { ...prev.movilidad, destino: e.target.value } 
                                }))}
                              />
                            </div>
                            <div>
                              <Label htmlFor="movilidad-monto">Monto</Label>
                              <Input
                                id="movilidad-monto"
                                type="number"
                                step="0.01"
                                value={expenseForm.movilidad.monto}
                                onChange={(e) => setExpenseForm(prev => ({ 
                                  ...prev, 
                                  movilidad: { ...prev.movilidad, monto: parseFloat(e.target.value) || 0 } 
                                }))}
                              />
                            </div>
                          </div>
                        )}
                      </div>

                      <div className="border rounded-lg p-4">
                        <div className="flex items-center space-x-2 mb-4">
                          <Checkbox
                            id="viaticos"
                            checked={expenseForm.viaticos.enabled}
                            onCheckedChange={(checked) => 
                              setExpenseForm(prev => ({ 
                                ...prev, 
                                viaticos: { ...prev.viaticos, enabled: checked as boolean } 
                              }))
                            }
                          />
                          <Label htmlFor="viaticos" className="font-medium">Viáticos</Label>
                        </div>
                        {expenseForm.viaticos.enabled && (
                          <div className="space-y-4 ml-6">
                            <div>
                              <Label>Hospedaje</Label>
                              <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mt-2">
                                <Input
                                  placeholder="Lugar"
                                  value={expenseForm.viaticos.hospedaje.lugar}
                                  onChange={(e) => setExpenseForm(prev => ({ 
                                    ...prev, 
                                    viaticos: { 
                                      ...prev.viaticos, 
                                      hospedaje: { ...prev.viaticos.hospedaje, lugar: e.target.value } 
                                    } 
                                  }))}
                                />
                                <Input
                                  type="number"
                                  step="0.01"
                                  placeholder="Monto"
                                  value={expenseForm.viaticos.hospedaje.monto}
                                  onChange={(e) => setExpenseForm(prev => ({ 
                                    ...prev, 
                                    viaticos: { 
                                      ...prev.viaticos, 
                                      hospedaje: { ...prev.viaticos.hospedaje, monto: parseFloat(e.target.value) || 0 } 
                                    } 
                                  }))}
                                />
                              </div>
                            </div>
                            <div>
                              <Label>Alimentos</Label>
                              <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mt-2">
                                <Input
                                  type="number"
                                  step="0.01"
                                  placeholder="Desayuno"
                                  value={expenseForm.viaticos.alimento.desayuno}
                                  onChange={(e) => setExpenseForm(prev => ({ 
                                    ...prev, 
                                    viaticos: { 
                                      ...prev.viaticos, 
                                      alimento: { ...prev.viaticos.alimento, desayuno: parseFloat(e.target.value) || 0 } 
                                    } 
                                  }))}
                                />
                                <Input
                                  type="number"
                                  step="0.01"
                                  placeholder="Almuerzo"
                                  value={expenseForm.viaticos.alimento.almuerzo}
                                  onChange={(e) => setExpenseForm(prev => ({ 
                                    ...prev, 
                                    viaticos: { 
                                      ...prev.viaticos, 
                                      alimento: { ...prev.viaticos.alimento, almuerzo: parseFloat(e.target.value) || 0 } 
                                    } 
                                  }))}
                                />
                                <Input
                                  type="number"
                                  step="0.01"
                                  placeholder="Cena"
                                  value={expenseForm.viaticos.alimento.cena}
                                  onChange={(e) => setExpenseForm(prev => ({ 
                                    ...prev, 
                                    viaticos: { 
                                      ...prev.viaticos, 
                                      alimento: { ...prev.viaticos.alimento, cena: parseFloat(e.target.value) || 0 } 
                                    } 
                                  }))}
                                />
                              </div>
                            </div>
                          </div>
                        )}
                      </div>

                      <div className="border rounded-lg p-4">
                        <div className="flex items-center space-x-2 mb-4">
                          <Checkbox
                            id="compras"
                            checked={expenseForm.compras.enabled}
                            onCheckedChange={(checked) => 
                              setExpenseForm(prev => ({ 
                                ...prev, 
                                compras: { ...prev.compras, enabled: checked as boolean } 
                              }))
                            }
                          />
                          <Label htmlFor="compras" className="font-medium">Compras</Label>
                        </div>
                        {expenseForm.compras.enabled && (
                          <div className="space-y-4 ml-6">
                            <Textarea
                              placeholder="Descripción"
                              value={expenseForm.compras.descripcion}
                              onChange={(e) => setExpenseForm(prev => ({ 
                                ...prev, 
                                compras: { ...prev.compras, descripcion: e.target.value } 
                              }))}
                            />
                            <Input
                              type="number"
                              step="0.01"
                              placeholder="Monto"
                              value={expenseForm.compras.monto}
                              onChange={(e) => setExpenseForm(prev => ({ 
                                ...prev, 
                                compras: { ...prev.compras, monto: parseFloat(e.target.value) || 0 } 
                              }))}
                            />
                          </div>
                        )}
                      </div>
                    </div>

                    <div className="flex gap-2">
                      <Button type="submit">
                        {editingExpense ? 'Actualizar' : 'Agregar'} Gasto
                      </Button>
                      {editingExpense && (
                        <Button type="button" variant="outline" onClick={resetExpenseForm}>
                          Cancelar
                        </Button>
                      )}
                    </div>
                  </form>
                </CardContent>
              </Card>

              <Card>
                <CardHeader>
                  <CardTitle>Gastos del Proyecto</CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="space-y-2">
                    {selectedProjectData?.expenses.map((expense) => (
                      <div key={expense.id} className="flex items-center justify-between p-3 border rounded-lg">
                        <div>
                          <div className="font-medium">{expense.fecha}</div>
                          <div className="text-sm text-muted-foreground">
                            {expense.movilidad && `Movilidad: ${expense.movilidad.inicio} → ${expense.movilidad.destino} (${expense.movilidad.monto})`}
                            {expense.viaticos && `Viáticos: ${expense.viaticos.hospedaje?.monto || 0} + ${Object.values(expense.viaticos.alimento || {}).reduce((sum, val) => sum + (val || 0), 0)}`}
                            {expense.compras && `Compras: ${expense.compras.descripcion} (${expense.compras.monto})`}
                          </div>
                        </div>
                        <div className="flex gap-2">
                          <Button
                            size="sm"
                            variant="outline"
                            onClick={() => handleEditExpense(expense)}
                          >
                            <Edit className="h-4 w-4" />
                          </Button>
                          <Button
                            size="sm"
                            variant="destructive"
                            onClick={() => deleteExpense(selectedProject, expense.id)}
                          >
                            <Trash2 className="h-4 w-4" />
                          </Button>
                        </div>
                      </div>
                    ))}
                  </div>
                </CardContent>
              </Card>
            </>
          )}
        </TabsContent>
      </Tabs>
    </div>
  );
}
